import React, { useRef, useState } from 'react';
import { fabric } from 'fabric';

import '../../../css/diary_draw.scss'
const DiaryDrawPage = ({ onSaveDrawing }) => {
    const canvasRef = useRef(null);
    const [canvas, setCanvas] = useState(null);
    const [textValue, setTextValue] = useState('');
    const [drawingMode, setDrawingMode] = useState(false);
    const [isErasing, setIsErasing] = useState(false);
    const [selectedColor, setSelectedColor] = useState('#000000');
    const [penColor, setPenColor] = useState('#000000');
    const [penWidth, setPenWidth] = useState(1)
    const [eraseWidth, setEraseWidth] = useState(1)
    //  
    const [hasDrawing, setHasDrawing] = useState(false);
    // Canvas 
    const initializeCanvas = () => {
        if (!canvasRef.current) return; //   
        if (!canvas) { //    
            
            const newCanvas = new fabric.Canvas(canvasRef.current, {
                isDrawingMode: false,
                selection: true,
            });
            setCanvas(newCanvas);
            bindCanvasEvents(newCanvas); //   
            setHasDrawing(true);
        } else { //    
            canvas.clear();
        }
    };
    //  
    const addText = () => {
        if (!canvas || !textValue) return;
        const text = new fabric.Textbox(textValue, {
            left: 100,
            top: 100,
            fontSize: 20,
            fontFamily: 'Dongle-Regular',
            fill: selectedColor, //   
        });
        canvas.add(text);
    };
//  
const addImage = (file) => {
    if (!canvas || !file) return;
  
    const reader = new FileReader();
    reader.onload = function (event) {
      const imgObj = new Image();
      imgObj.src = event.target.result;
  
      imgObj.onload = function () {
        //    (   )
        const canvasWidth = canvas.getWidth();  //  
        const canvasHeight = canvas.getHeight();  //  
  
        const imgWidth = imgObj.width;
        const imgHeight = imgObj.height;
  
        //   
        const imgRatio = imgWidth / imgHeight;
        const canvasRatio = canvasWidth / canvasHeight;
  
        let scaledWidth, scaledHeight;
  
        //       
        if (imgRatio > canvasRatio) {
          scaledWidth = canvasWidth;
          scaledHeight = scaledWidth / imgRatio;
        } else {
          scaledHeight = canvasHeight;
          scaledWidth = scaledHeight * imgRatio;
        }
  
        //  
        const image = new fabric.Image(imgObj, {
          left: (canvasWidth - scaledWidth) / 2,  //  
          top: (canvasHeight - scaledHeight) / 2,  //  
          scaleX: scaledWidth / imgWidth,  //   
          scaleY: scaledHeight / imgHeight,  //   
        });
  
        //   
        canvas.add(image);
      };
    };
  
    reader.readAsDataURL(file);
  };
    //  
    const addShape = (shapeType) => {
        if (!canvas) return;
        let shape;
        switch (shapeType) {
            case 'circle':
                shape = new fabric.Circle({ radius: 50, fill: selectedColor, left: 100, top: 100 });
                break;
            case 'rectangle':
                shape = new fabric.Rect({ width: 100, height: 50, fill: selectedColor, left: 100, top: 100 });
                break;
            //    
            default:
                return;
        }
        canvas.add(shape);
    };
    // Canvas  
    const clearCanvas = () => {
        if (!canvas) return;
        canvas.clear();
    };
    //    
    const toggleDrawingMode = () => {
        if (!canvas) return;
        canvas.isDrawingMode = !drawingMode;
        setDrawingMode(!drawingMode);
    };
    //    
    const handleMouseDown = (event) => {
        if (!canvas || !drawingMode) return;
        
        const pointer = canvas.getPointer(event.e);
        const { x, y } = pointer;
        
        //   
        if (drawingMode) {
            const path = new fabric.Path(`M ${x} ${y}`, {
                stroke: penColor, //   
                strokeWidth: 2,
                fill: 'transparent',
            });
            canvas.add(path);
            canvas.renderAll();
            path.path.push(`L ${x} ${y}`);
            path.setCoords();
        }
    };
    //  
    const handleColorChange = (e) => {
        setSelectedColor(e.target.value);
    };
    //   
    const handlePenColorChange = (e) => {
        setPenColor(e.target.value);
        if (canvas && canvas.isDrawingMode) {
            canvas.freeDrawingBrush.color = e.target.value;
        }
    };
    //   
const handlePenWidthChange = (width) => {
    if(!canvas) return;
    setPenWidth(width);
    canvas.freeDrawingBrush.width = width;
};
//  
const handleEraseWidthChange = (width) => {
    if(!canvas) return;
    setEraseWidth(width);
    canvas.freeDrawingBrush.width = width;
};
    //  
const toggleEraser = () => {
    if (!canvas) return;
    if (!isErasing) {
        canvas.freeDrawingBrush.color = '#FFF4F6'; //        
        canvas.isDrawingMode = true; //   
    } else {
        canvas.freeDrawingBrush.color = selectedColor; //      
        canvas.isDrawingMode = drawingMode; //   
    }
    setIsErasing(!isErasing); //   
};
    // Canvas  
    const bindCanvasEvents = (canvas) => {
        canvas.on('mouse:down', handleMouseDown);
        //     
    };
    //       
    const saveDrawingData = () => {
        if (!canvas) return;
        if (hasDrawing) {
            const drawingData = canvas.toDataURL({ format: 'png' }); // Base64   
            const base64String = drawingData.split(',')[1] // "data:image/png;base64, 
            onSaveDrawing(base64String); //   
        } else {
            onSaveDrawing(null); //   NULL  
        }
    };
    return (
        <div>
            <div className='canvas'>
            <canvas ref={canvasRef} ></canvas>
            </div>

            <div className='diaryDraw_form_content'>
                <button className="darw_start"type="button" onClick={initializeCanvas}> </button>

                <div className='draw_add_1'>
                    <input type="text" value={textValue} onChange={(e) => setTextValue(e.target.value)} placeholder="Enter text" />
                    <button type="button" onClick={addText}> </button>
                </div>
                <div className='draw_add_2'>
                    <input type="file" id="file" accept="image/*" onChange={(e) => addImage(e.target.files[0])} />
                </div>
                
                <div className='draw_add_3'>
                    <button type="button" onClick={() => addShape('circle')}> </button>
                    <button type="button" onClick={() => addShape('rectangle')}> </button>
                    <input type="color" value={selectedColor} onChange={handleColorChange} />

                </div>

                <div className='draw_pen'>
                    <button type="button" onClick={clearCanvas}> </button>
                    <button type="button" onClick={toggleDrawingMode}>{drawingMode ? ' ' : ' '}</button>
                    <button type="button" onClick={toggleEraser}>{isErasing ? ' ' : ' '}</button>
                </div>
                <div className='draw_pen2'>
                    <input type="color" value={penColor} onChange={handlePenColorChange} />
                    <input type="range" min="1" max="10" value={penWidth} onChange={(e) => handlePenWidthChange(parseInt(e.target.value))} />
                    <input type="range" min="1" max="10" value={eraseWidth} onChange={(e) => handleEraseWidthChange(parseInt(e.target.value))} />
                </div>
                    


                <button className="draw_finish" type="button" onClick={saveDrawingData}> </button>
            </div>
            </div>
    );
};
export default DiaryDrawPage;