import React from 'react'
import { Link } from 'react-router-dom'
import '../../../css/navbar.scss'

import { useDispatch } from 'react-redux'
import {logoutUser } from '../../../_actions/user_action';
import { useNavigate } from 'react-router-dom'

function NavBar() {
  const navigate = useNavigate()
  const dispatch= useDispatch()
      //
      const onLogoutHandler = () => {
        dispatch(logoutUser())
          .then(response => {
            //         .
            navigate('/')

          })
          .catch(error => {
            //          .
            //console.error('Logout Error:', error);
          });
      };

  const onLinkHandler1= () =>{
    if(localStorage.getItem("is_logined")){
      navigate('/myinfo')
    }
    else{
      alert("  !")
      navigate('/')
    }
  }
  const onLinkHandler2= () =>{
    if(localStorage.getItem("is_logined")){
      navigate('/diaryList')
    }
    else{
      alert("  !")
      navigate('/')
    }
  }
  return (
    <div className='navbar'>
      <div className='navbar_content'>
        <div className='navbar_title'>...</div>
        <div className='navbar_sub'><br/> <br/>..ww</div>


        <ul className='navbar_menu'>
          {localStorage.getItem("is_logined") ? (
                    <li onClick={onLogoutHandler}><span></span><span>. . . 3</span></li>
                  ) : (
                  <Link to='/'>
                  <li className='link'><span></span><span>. . . 3</span></li>
                  </Link>
                  )}
                  <li onClick={onLinkHandler1}><span></span><span>. . . 12</span></li>
                  <li onClick={onLinkHandler2}><span> </span><span>. . . 34</span></li>
        </ul>

        <div className='navbar_sub right'>  <br/> </div>
      </div>

    </div>

  )
}

export default NavBar