
import React from 'react'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { loginUser } from '../../../_actions/user_action';
import '../../../css/login.scss'

import SlideMenu from '../NavBar/SlideMenu'
import NavBar from '../NavBar/NavBar';
import { Link } from 'react-router-dom';


function LoginPage() {
    const dispatch= useDispatch()
    const navigate = useNavigate();
    const [Username, setUsername] = useState("")
    const [Password, setPassword] = useState("")

    const onUsernameHandler = (e) =>{
        setUsername(e.currentTarget.value)
    }
    const onPasswordHandler = (e) =>{
        setPassword(e.currentTarget.value)
    }




    const onSubmitHandler = (e) =>{
        e.preventDefault()

        if(Username === ""){
            alert(" ")
            return;
        }
        if(Password === ""){
            alert(" ")
            return;
        }
        let body = {
            username: Username,
            password: Password
        }

    // dispatch      
    dispatch(loginUser(body))
        .then(response => {
            if (response.status === 200) {
                alert(' ');
                navigate('/diaryList');
            } 
        })
        .catch(error => {
            alert('  ! ');
        });
    }
  return (
    <div className='Login'>
    <div className='inner_navbar'>
        <NavBar/>
    </div>
    <div className='login_formbox'>
        <div className='moblie_menu'>
            <SlideMenu/>
        </div>
        <div className='login_inner_formbox'>
            <div className='inner_title'>
                <div className='title'><br/>
                                  ...</div>
            </div>
    <form className='login_formbox_content' onSubmit={onSubmitHandler}>
        <div className='login_card'>
            <div className='login_card_info'>
                <div>ID card</div>
                <div>Diary Exchange<br/>Club</div>
            </div>
            <img className="profile"src={process.env.PUBLIC_URL + '/profile.png'}alt="" />
        </div>

        <label>Username</label>
        <input className="login_input"type="text" value={Username} onChange={onUsernameHandler}/>
        <label>Password</label>
        <input className="login_input"type="password" value={Password} onChange={onPasswordHandler}/>

        <div className='login_sub'>
                .
            <img className="stamp"src={process.env.PUBLIC_URL + '/stamp.png'} alt=""/>
        </div>

        <div className='login_sub2'>
            <Link to={"/register"}>
                   .
            </Link>
            <span></span>
            <Link to={"/password"}>
                   / .
            </Link>
        </div>

        
    </form>
    <button className='login_submit_button' type="submit" onClick={onSubmitHandler}>
            
    </button>
    </div>
    </div>
    </div>


  
  )

}

export default LoginPage