import React from 'react'
import { useState, useRef,useEffect } from 'react';
import '../../../css/slidemenu.scss';
import { useDispatch } from 'react-redux'
import {logoutUser } from '../../../_actions/user_action';
import { Link,useNavigate } from 'react-router-dom'

function SlideMenu() {

const dispatch= useDispatch()
const navigate = useNavigate()

//
const onLogoutHandler = () => {
  dispatch(logoutUser())
    .then(response => {
      navigate('/')
    })
    .catch(error => {
      //          .
      //console.error('Logout Error:', error);
    });
};
//
const onLinkHandler1= () =>{
  if(localStorage.getItem("is_logined")){
    navigate('/myinfo')
  }
  else{
    alert("  !")
    navigate('/')
  }
}
const onLinkHandler2= () =>{
  if(localStorage.getItem("is_logined")){
    navigate('/diaryList')
  }
  else{
    alert("  !")
    navigate('/')
  }
}

//
const [isOpen, setIsOpen] = useState(false); //  /   
const outside = useRef(null); //     ref

const toggleSide = () => {    setIsOpen(true);  };
//        
const handleClickOutside = (event) => {
    if (outside.current && !outside.current.contains(event.target)) {
      setIsOpen(false);
    }
  };

  //          
  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);


  return (
    <div className='mobile_navbar'>
        <div className='mobile_navbar_content'>
            <div onClick={toggleSide} className='menuButton'>
         {
        <div id="sidebar"
        ref={outside} className={isOpen ? 'open' : ''}>
            <div className='mobile_navbar_title'>...</div>
            <div className='mobile_navbar_sub'><br/> <br/>..ww</div>

            <ul  className='mobile_navbar_menu'>
                {localStorage.getItem("is_logined") ? (
                    <li onClick={onLogoutHandler}><span></span><span>. . . 3</span></li>
                    ) : (
                    <Link to='/'>
                    <li className='link'><span></span><span>. . . 3</span></li>
                    </Link>
                    )}
                    <li onClick={onLinkHandler1}><span></span><span>. . . 12</span></li>
                    <li onClick={onLinkHandler2}><span> </span><span>. . . 34</span></li>
            </ul>

            <div className='mobile_navbar_sub mobileRight'>  <br/> </div>
        </div>
        }

        </div>
        </div>
    </div>


  )
}

export default SlideMenu